@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  @import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&family=Roboto:wght@100&display=swap");
  @import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;500;600;700&display=swap');

  

  @layer base {
    body {
      @apply font-SOURCECODE;
    }
    li {
      @apply px-4;
      @apply cursor-pointer;
    }
    li:hover{
      @apply text-yellow-600
    }
 
  }


  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
  }
  /* container  */
  .container .card {
    position: relative;
    width: 250px;
    height: 250px;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 50%;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.3);
    background-image: linear-gradient(110.3deg, black 33.6%, gray 80%);
    transition: all 0.3s ease-in-out;
  }
  
  .card:hover {
    width: 350px;
    height: 420px;
    border-radius: 5px;
    box-shadow: 0px 1px 35px 0px rgba(0, 0, 0, 0.3);
  }
  
  .card .content {
    text-align: center;
  }
  
  /* wrapper */
  .content .photo img {
    display: block;
    position: relative;
    z-index: 20;
    width: 200px;
    height: 200px;
    margin: 25px auto;
    object-fit: cover;
    border-radius: 50%;
    border: 5px solid #fff;
    transform: all 0.3s ease;
  }
  /*
  .card:hover .content img.active{
      height: 470px;
      width: 350px;
      margin: 0px auto;
      border: none;
      border-radius: 5px;
    }
  */
  
  /* Styling the profile's main content */
  .content .details .name {
    display: block;
    font-size: 30px;
    font-weight: 500;
    padding: 10px;
    line-height: 25px;
    color: #fff;
  }
  
  .content .details .place {
    display: block;
    font-size: 16px;
    margin: 10px 0;
    line-height: 0;
    color: #fff;
  }
  
  .content .details .job {
    font-size: 17px;
    padding: 1px 20px 10px 20px !important;
    margin-top: 20px;
    color: #fff;
  }
  
  /* Styling the buttons */
  .content .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .buttons button {
    width: 150px;
    height: 40px;
    margin: 0 5px;
    font-size: 17px;
    font-weight: 500;
    border-radius: 5px;
    border: none;
    outline: none;
    color: #7c195d;
    background: #fff;
    cursor: pointer;
    transition: all 0.3s;
  }
  
  .buttons button:hover {
    transform: scale(0.95);
  }
  
  /* Styling the social media icons */
  .container .card .social {
    position: absolute;
    top: 10px;
    left: 10px;
  }
  
  .container .card .social.active {
    display: none;
  }
  
  .card .social a {
    display: block;
    width: 35px;
    height: 35px;
    margin: 7px 0;
    color: #d568f7;
    background: #fff;
    border-radius: 50%;
    opacity: 0;
    margin-left: -100px;
    transition: all 0.5s ease;
  }
  
  .card:hover .social a {
    opacity: 1;
    margin-left: 7px;
  }
  
  .card:hover .social a:nth-child(2) {
    transition-delay: 0.2s;
  }
  
  .card:hover .social a:nth-child(3) {
    transition-delay: 0.4s;
  }
  
  .social i {
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    transition: all 0.3s;
  }
  
  .social i:hover {
    transform: scale(0.8);
  }
